import React from "react";
import { Link } from "react-router-dom";

class MainMenu extends React.Component {
	choices = [
		{
			code: "graph",
			name: "Grafici",
			link: `/graph/${this.props.lastQuery == "" ? this.props.defaultQueryParams : this.props.lastQuery}`
		},
		{
			code: "map",
			name: "Mappa",
			link: `/map/${this.props.lastQuery == "" ? this.props.defaultQueryParams : this.props.lastQuery}`
		},
		{
			code: "raw",
			name: "Database",
			link: `/raw`
		}
	];

	render() {
		return (
			<aside className="d-none col-sm-4 col-xl-2 col-12 mt-3 bg-light border red-outline" id="main-menu">
				<div className="list-group list-group-flush main-nav">
					<h3 className="py-2">Vai a...</h3>
					{this.choices.map((choice) => {
						const defaultClasses = "list-group-item list-group-item-action";
						if (this.props.selectedMode == choice.code)
							return (
								<p
									className={defaultClasses + " selected-item hand"}
									onClick={() => {
										let sidebar = document.getElementById("main-menu");
										let page = document.getElementById("main-page");
										sidebar.classList.replace("d-block", "d-none");
										page.className = "col-12"
									}}
									key={choice.code}
								>
									{choice.name}
								</p>
							);
						else
							return (
								<Link className={defaultClasses + " bg-light"} to={choice.link} key={choice.code}>
									{choice.name}
								</Link>
							);
					})}
				</div>
			</aside>
		);
	}
}

export default MainMenu;
